bkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;line-height:1.5}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}h1,h2,h3{text-align:center}.readable-content{margin:0 auto;max-width:45em}.container{text-align:center;padding:1em 0.5em}.container A:link{text-decoration:none;color:#0788df}.container A:visited{text-decoration:none;color:#04a885}.container A:active{-webkit-text-decoration:#eb654d;text-decoration:#eb654d}.container A:hover{text-decoration:underline;color:#ED6E59}section>div>div>ul>li>div{display:inline-flex !important;width:100%}

